.navbar {
  .logo {
    position: relative;
    right: 15px;
    top: 6px;
    .logo1 {
      position: absolute;
      left: -15px;
      bottom: 15px;
      z-index: -1; } }
  .navbar-brand {
    h2 {
      font-family: 'Baloo Tamma 2', cursive;
      span {
        &:first-of-type {
          position: relative;
          top: 12px;
          color: #6c63ff; } }
      span {
        &:last-of-type {
          font-size: 18px; } } } }
  .navbar-nav {
    li {
      a {
        @extend %button;
        margin-right: 15px;
        background-color: #5b5879;
        &::after {
          background-color: #6c63ff;
          width: 0;
          height: 100%; }
        &:hover {
          &::after {
            width: 100%; } }
        span {
          position: relative;
          z-index: 3; } } } } }



