// start header

.header {
    .layer2 {
        position: absolute;
        right: 0;
        z-index: -1;
        top: 100px; }
    .layer1 {
        position: absolute;
        left: -83px;
        z-index: -2;
        top: 10px; }
    p {
        @extend %font;
        font-size: 37px;
        width: 50%;
        margin-top: 30px;
        color: #3f3d56; }
    button {
        @extend %button;
        background-color: #fed253;
        border: none;
        margin-top: 40px;
        font-size: 23px;
        span {
            position: relative;
            z-index: 3; }
        &::after {
            height: 0;
            width: 100%; }
        &:hover {
            &::after {
                height: 100%;
                background-color: #d35400; } } } }

// end header

// start services

.services {
    .card-group {
        position: relative;
        margin-top: 205px;
        .card {
            margin-right: 20px;
            overflow: hidden;
            border-radius: 12px !important;
            border: none;
            &:nth-of-type(2) {
                position: relative;
                bottom: 44px; }
            &:hover {
                img {
                    height: 100%;
                    width: 100%;
                    top: 0%;
                    left: 0%; } } }
        .image {
            height: 220px;
            img {
                height: 105%;
                width: 105%;
                top: -2.5%;
                left: -2.5%;
                position: relative;
                transition: all .4s ease-in-out; } }
        .card-body {
            background-color: #3f3d56;
            h5 {
                color: #fed253; }
            p {
                color: white; } } } }


// end services

// start about

.about {
    h2 {
        margin-top: 50px; }
    .first {
        p {
            @extend %text;
            margin: 30px 10px; } }
    .second {
        h3 {
            @extend %font;
            margin: 50px 0 14px;
            span {
                position: relative;
                right: 10px;
                .pol2 {
                    position: absolute;
                    right: -2px;
                    top: 5px;
                    opacity: .7; }
                .pol1 {
                    opacity: .8; } } }
        p {
            @extend %text; }
        button {
            background: none;
            border: none;
            border-bottom: 8px solid #fed253; } } }

// end about

// start project

.project {
    background:url('../img/shape 2.png') no-repeat {}
    background-size: 103%;
    position: relative;
    margin-top: 50px;
    padding-bottom: 130px;
    >img {
        position: absolute;
        width: 100%;
        opacity: .3;
        top: 30px; }
    h2 {
        padding-top: 90px;
        @extend %font;
        color: white;
        position: relative;
        z-index: 1; }
    .arrow {
        position: relative;
        height: 40px;
        bottom: 45px;
        .left,.right {
            position: absolute;
            display: inline-block;
            width: 61px;
            height: 61px;
            cursor: pointer; }
        .left {
            background: url('../img/Polygona1.png') no-repeat;
            right: 65px;
            i {
                line-height: 61px;
                padding-left: 14px;
                color: white; } }
        .right {
            background: url('../img/Polygona2.png') no-repeat;
            right: 0;
            i {
                line-height: 61px;
                padding-left: 14px;
                color: white; } } }
    .image {
        height: 270px;
        width: 250px;
        border: 2px solid white;
        border-radius: 10px;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            position: relative;
            transition: all .5s ease-in-out;
            top: 0;
            left: 0; }
        &:hover {
            img {
                width: 120%;
                height: 120%;
                left: -10%;
                top: -10%; } } } }


